import React from 'react';
import ReactDOM from 'react-dom/client';
import './theme/index.scss';
import App from './App';
import reportWebVitals from './utils/reportWebVitals.utils';
import { DAppProvider, DAppProviderProps } from '@usedapp/core';
import { SupportedChains } from './services/web3/chains';

const DAppProviderConfig: DAppProviderProps['config'] = {
  networks: Object.values(SupportedChains),
  readOnlyUrls: Object.values(SupportedChains).reduce(
    (acc, chain) => Object.assign(acc, { [chain.chainId]: chain.rpcUrl }),
    {}
  ),
};

const root = ReactDOM.createRoot(document.getElementById('root')!);
root.render(
  <React.StrictMode>
    <DAppProvider config={DAppProviderConfig}>
      <App />
    </DAppProvider>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals(console.info);
