import { Contract } from 'ethers';
import { SupportedChainId } from './chains';
import {
  ERC20__factory,
  factories,
  Frankencoin__factory,
  ReservePool__factory,
  StablecoinBridge__factory,
} from './generated';

export type ContractMapEntry<C extends Contract> = {
  [key in SupportedChainId]?: C;
};

/**
 * Generates a ContactMapEntry, while keeping strong typing
 */
const connect = <F extends typeof factories[keyof typeof factories]>(
  factory: F,
  addresses: { [key in SupportedChainId]?: string }
): ContractMapEntry<ReturnType<F['connect']>> =>
  Object.keys(addresses).reduce((acc, chainId) => {
    acc[chainId] = factory.connect(addresses[chainId], undefined);
    return acc;
  }, {});

// No type set here so the ContractName works as expected.
export const Contracts = {
  Frankencoin: connect(Frankencoin__factory, {
    [SupportedChainId.EthereumSepolia]:
      '0x5037Df9b4Fb98FE5d4e106Ba615D45d625138FAF',
  }),
  XCHF: connect(ERC20__factory, {
    [SupportedChainId.EthereumMainnet]:
      '0xb4272071ecadd69d933adcd19ca99fe80664fc08',
    [SupportedChainId.EthereumSepolia]:
      '0x4bfc8c42362C7626D0555cA273Cc12c1580E8A2d',
  }),
  ReservePool: connect(ReservePool__factory, {
    [SupportedChainId.EthereumSepolia]:
      '0xA365E1B43Dd4F6fbAD6b450AbFEf4C4C2db392d9',
  }),
  StableCoinBridgeXCHF: connect(StablecoinBridge__factory, {
    [SupportedChainId.EthereumSepolia]:
      '0xF8e20518393FF2B8Db6C00adBAc2c48956041c41',
  }),
};

export type ContractName = keyof typeof Contracts;
