import { useEthers } from '@usedapp/core';
import { useMemo } from 'react';
import { SupportedChainId, SupportedChains } from '../services/web3/chains';

export const useChain = () => {
  const { chainId: walletChainId } = useEthers();

  const chainId = SupportedChainId[walletChainId]
    ? (walletChainId as SupportedChainId)
    : SupportedChainId.EthereumSepolia;

  return useMemo(
    () => ({
      walletChainId,
      chainId,
      chain: SupportedChains[chainId],
    }),
    [walletChainId, chainId]
  );
};
