import { useCall } from '@usedapp/core';
import { FC } from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { useChain } from '../../../hooks/useCurrentChain';
import { Contracts } from '../../../services/web3/contracts';
import { MainTemplate } from '../../4_templates/MainTemplate/MainTemplate';
import logo from '../../../assets/logo-256.png';
import styles from './LandingPage.module.scss';
import { formatUnits } from 'ethers/lib/utils';
import { SupportedCurrencies } from '../../../services/web3/chains';

type Props = {};

export const LandingPage: FC<Props> = () => {
  const { chainId } = useChain();

  const totalSupply = useCall(
    {
      contract: Contracts.Frankencoin[chainId],
      method: 'totalSupply',
      args: [],
    },
    { chainId: chainId as number, refresh: 'never' }
  );

  const reserve = useCall(
    {
      contract: Contracts.Frankencoin[chainId],
      method: 'reserve',
      args: [],
    },
    { chainId: chainId as number, refresh: 'never' }
  );

  return (
    <MainTemplate>
      <Container className={styles.host}>
        <Row>
          <Col xs={12} md={6} xl={4}>
            <img className={styles.heroImage} src={logo} alt='logo' />
          </Col>
          <Col xs={12} md={6} xl={8}>
            <Row>
              <h1 className={styles.title}>
                ZCHF, the on-chain diversified stablecoin.
              </h1>
              <h2 className={styles.callToAction}>
                Connect your wallet and get started!
              </h2>
            </Row>
            <Row>
              <Col>Current Supply:</Col>
              <Col>
                {formatUnits(
                  totalSupply?.value?.[0] || '0',
                  SupportedCurrencies.ZCHF.decimals
                )}
              </Col>
            </Row>
            <Row>
              <Col>Reserve Pool:</Col>
              <Col>
                {formatUnits(
                  reserve?.value?.[0] || '0',
                  SupportedCurrencies.ZCHF.decimals
                )}
              </Col>
            </Row>
          </Col>
        </Row>
      </Container>
    </MainTemplate>
  );
};
